import React from 'react';
import { Dropdown } from 'semantic-ui-react';
import { useHistory } from 'react-router-dom';
import axios from 'axios';

const BurgerButtonAdmin = () => {
    const history = useHistory();

    const disconnect = () => {
        const token = localStorage.getItem('token');
        axios
            .get('logout', { headers: {
                Authorization: 'Bearer ' + token,
            },        
          }) 
          .then((res) => {
              localStorage.removeItem('token');
              history.push('/');
          })
          .catch((err) => {
              console.log(err)
          })
    };

    return (
        <div className='burger-button'>
            <Dropdown item text='' icon='bars'>
                <Dropdown.Menu>
                <Dropdown.Item onClick={() => history.push('/dashboard')}>Liste des rparations</Dropdown.Item>
                    <Dropdown.Item onClick={() => history.push('/RepairSheetForm')}>Crer une fiche de rparation</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/ClientList')}>Liste des clients</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/clientForm')}>Crer une fiche client</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/archives')}>Liste des archives</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/workerForm')}>Crer une fiche employ</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/workerList')}>Liste des employs</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/PanneForm')}>Crer un modle de panne</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/PanneList')}>Liste des pannes</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/TagForm')}>Crer un tag</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/TagList')}>Liste des tags</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/actionForm')}>Crer une action</Dropdown.Item>
                    <Dropdown.Item onClick={()=> history.push('/actions')}>Liste des action</Dropdown.Item>
                    <Dropdown.Item onClick={disconnect} className='deconnect' >Dconnexion</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>
        </div>
    )
};

export default BurgerButtonAdmin;